.navbar {
    background-color: #f4f3ee;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: sticky;
    top: 0;
    z-index: 1000;
}
.logo {
     color: #333;
    font-size: 24px;
    text-decoration: none;
    font-weight: bold;
    text-decoration: none;
}
.flex1 {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar a {
    color: #333;
    margin-left: 30px;
    text-decoration: none;
}
.nav-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 1200px;
    padding: 0 20px;
}


  .navbar button {
    border-radius: 5px;
    font-size: 15px;
     font-weight: bold;
  }
 .menu-icon {
    display: none;
    font-size: 28px;
    cursor: pointer;
    color: #000;
    width: 30px;
    height: 30px;
    margin-right: 30px;
}
.nav-menu {
    color: #f4f3ee;
    display: flex;
    list-style: none;
    text-align: center;
}
.nav-item {
    margin: 0 10px;
}
.nav-links {
    color: #333;
    text-decoration: none;
    padding: 10px 18px;
    border-radius: 5px;
    transition: background 0.3s;
    margin-right: 15px;
}
.nav-links:hover {
    background-color: #333;
    color: #f4f3ee;
}
  
  .control-btn button {
   background: none;
   padding: 0;
   width: 50px;
   height: 50px;
   border-radius: 50%;
   cursor: pointer;
   border: 2px solid rgba(255, 255, 255, .3);
  }


  /* -- responsive style */
  @media only screen and (max-width:768px) {

    .navbar nav .flex1 {
      display: none;
    }
      .nav-menu.active {
        left: 0;
        opacity: 1;
    }
     .menu-icon {
        display: block;
        
    }
     .nav-item {
        margin: 20px 0;
    }
 
    .nav-links {
        font-size: 20px;
        color: #f4f3ee;
    }
   
  
  }










